import React from "react";
import styled from "styled-components";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import Footers from "../layout/Footers"; 


function Ticket() {
  const navigate = useNavigate();
  const bookingDate = useSelector((state) => state.booking)  //    

  return (
  <>
  <Body>
    <TicketHeader> </TicketHeader>
    <Ticketinfo>
      <PaymentDone>  .</PaymentDone>
      <Movieinfo>
        <Poster>
        <img src={bookingDate.Img}></img>
        </Poster>
        <Theater>
          <Theaterinfo>
            <Movietitle>{bookingDate.Title}</Movietitle>
            <Theaterchoose>
            <span> </span>
            <span>{bookingDate.Town}</span>
            </Theaterchoose>

            <DateChoose>
            <span> </span>
            <span>{bookingDate.Date}</span>
            </DateChoose>

            <DateChoose>
            <span> </span>
            <span>{bookingDate.Time}</span>
            </DateChoose>

            <NumberChoose>
            <span> </span>
            <span>1</span>
            </NumberChoose>

            <SeatChoose>
            <span> </span>
            <span>{bookingDate.Seat}</span>
            </SeatChoose>
        </Theaterinfo>

        <Print>
          <Button1 type="button" onClick={() => {navigate("/movielist");}}> </Button1>
          <Button2 type="button" onClick={() => {navigate("/movielist");}}> SMS</Button2>
          <Button3 type="button" onClick={() => {navigate("/movielist");}}>/</Button3>
          </Print>
        </Theater>
      </Movieinfo>
      <Border></Border>
      <Warning>
      <Warning1>
      <img src="http://img.cgv.co.kr/CGV_RIA/Ticket/image/reservation/step4/text_ticket_instructions.png"></img>
      </Warning1>

      <ul>
        <li>CJ ONE    .<em>(,   )</em></li>
          ,       .<br /><em>(    ,       )</em>
        <li>       .</li>
        <li>       .</li>
      </ul>

      </Warning>
    </Ticketinfo>
  </Body>
  <Footers/>
  </>
    ) ;
};

export default Ticket;


const Body = styled.div`
border: none;
height: 600px;
width: 1000px;
display: block;
margin: auto;
margin-top: 50px;
background: #FDFCF0;
`;

const TicketHeader=styled.div`
background: #444444;
border: none;
width: 1000px;
height: 40px;
color: #dddddd;
font-weight: bold;
align-items: center;
justify-content: center;
display: flex;
`;

const Ticketinfo = styled.div`
border: none;
height: 500px;
width: 900px;
display: block;
margin-left: 55px;
margin-top: 10px;
`;

const PaymentDone = styled.div`
border: none;
height: 90px;
width: 900px;
display: flex;
align-items: center;
justify-content: center;
font-size: x-large;
font-weight: bold;
`;

const Movieinfo = styled.div`
border: none;
height: 300px;
width: 900px;
display: flex;
`;

const Poster= styled.div`
border: none;
height: 250px;
width: 200px;
display: block;
align-items: center;
justify-content: center;
font-size: x-large;
font-weight: bold;
& img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
}
`;

const Theater = styled.div`
border: none;
height: 300px;
width: 650px;
display: block;
margin-left: 55px;
`;

const Border = styled.div`
border: solid 1px gray;
background-color: gray;
width: 900px;
display: block;
margin-top: 10px;
`;

const Theaterinfo = styled.div`
border: none;
height: 200px;
width: 650px;
display: block;
`;

const Print = styled.div`
border: none;
height: 95px;
width: 650px;
display: flex;
`;

const Movietitle=styled.div`
width: 150px;
height: 30px;
font-size: large;
font-weight: bold;
`;


const Theaterchoose=styled.div`
border: none;
width: 400px;
height: 30px;
font-weight: bold;
`;

const DateChoose=styled.div`
border: none;
width: 250px;
height: 30px;
font-weight: bold;
`;

const NumberChoose=styled.div`
border: none;
width: 250px;
height: 30px;
font-weight: bold;
font-size:  medium;
`;

const SeatChoose=styled.div`
border: none;
width: 250px;
height: 20px;
font-weight: bold;
font-size:  medium;
margin-bottom: 10px;
`;

const Payment=styled.div`
border: none;
width: 250px;
height: 30px;
font-weight: bold;
font-size:  medium;
`;

const Button1=styled.div`
border: none;
height: 80px;
width: 200px;
margin-top: 15px;
border-radius: 20px;
background-color: #B90000 ;
color: white ;
font-weight: bold;
font-size:  large;
text-align: center;
line-height : 80px;
`;

const Button2=styled.div`
height: 80px;
width: 200px;
margin-top: 15px;
margin-left: 15px;
border-radius: 20px;
background-color: #333 ;
color: white ;
font-weight: bold;
font-size:  large;
text-align: center;
line-height : 80px;
`;

const Button3=styled.div`
border: none;
height: 80px;
width: 200px;
margin-top: 15px;
margin-left: 15px;
border-radius: 20px;
background-color: #333 ;
color: white ;
font-weight: bold;
font-size:  large;
text-align: center;
line-height : 80px;
`;

const Warning=styled.div`
border:  none;
width: 900px;
height: 125px;
font-weight: bold;
font-size:  medium;
display: flex;
`;

const Warning1=styled.div`
border:  none;
width: 150px;
height: 25px;
font-weight: bold;
font-size:  medium;
display: flex;
margin-top: 20px;
`;

