import React from "react";
import styled from "styled-components";
import { useDispatch } from "react-redux";
import { getKakao } from "../redux/modules/user";
import { useEffect } from "react";

const KakaoLoading = () => {
  const dispatch = useDispatch();
  const params = new URLSearchParams(window.location.search); // ?    //?code=..     code .
  let code = params.get("code");
  // console.log(code); // localhost3000/loading/?code= ....   code= "~~~" 

  useEffect(() => {
    //  code   
    dispatch(getKakao(code));
  }, []);

  return (
    <Div>
      <img src="https://cdn.jjalbot.com/2021/12/tPaZBIZ-K/tPaZBIZ-K.gif" />
    </Div> //  gif
  );
};

export default KakaoLoading;

const Div = styled.div`
  position: absolute;

  top: 50%;
  left: 50%;
  height: 300px;
  width: 400px;
  margin: -150px 0 0 -200px;
  padding: 5px;
`;
