export const user = createSlice({
  name: "user",
  initialState: {
    is_Login: false,
    error: null,
  },
  reducers: {
    // 
    loginCheck: (state) => {
      //app.js       state is_Login 
      state.is_Login = true;
    },
  },
});

export const { loginCheck } = user.actions;
export default user.reducer;
