import { instance } from "../../shared/api";
import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";

//  
export const getMovies = createAsyncThunk(
  "movies/getMovies",
  async (_, thunkAPI) => {
    try {
      const data = await instance.get("/movies");
      return data.data.data;
    } catch (error) {
      return thunkAPI.rejectWithValue(error);
    }
  }
);

//  
export const getDetails = createAsyncThunk(
  "details/getDetails",
  async (payload, thunkAPI) => {
    try {
      const data = await instance.get(`/movie/${payload}`); //movie id  
      return data.data.data;
    } catch (error) {
      return thunkAPI.rejectWithValue(error);
    }
  }
);

//  
export const getMypage = createAsyncThunk(
  "mypage/getMypage",
  async (_, thunkAPI) => {
    try {
      const data = await instance.get("/user/movielog");

      return data.data.data;
    } catch (error) {
      return thunkAPI.rejectWithValue(error);
    }
  }
);

//
export const onLikePost = createAsyncThunk(
  "like/onLikePost",
  async (payload, thunkApI) => {
    try {
      const data = await instance.post(`/movie/like/${payload}`);

      return data.data.data; // "like success" 
    } catch (error) {
      return thunkApI.rejectWithValue(error);
    }
  }
);

export const movies = createSlice({
  name: "movies", //components data extra     initialState .
  initialState: {
    movies: [],
    detail: {
      title: "",
      titleEng: "",
      img: "",
      date: "",
      director: "",
      actor: "",
      rate: "",
      genderRate: "",
      genre: "",
      base: "",
      detail: "",
      status: 2,
    },
    like: [],
    mylist: [],
    error: null,
  },
  reducers: {},

  extraReducers: {
    [getMovies.pending]: (state) => {
      state.isLoading = true; //     true .
    },
    [getMovies.fulfilled]: (state, action) => {
      // console.log(action);        //     
      state.isLoading = false; //   , false .
      state.movies = action.payload; // Store  movies   movies .
      // console.log(state.movies);  // state.movies    useSelector  !
    },
    [getMovies.rejected]: (state, action) => {
      state.isLoading = false; //  ,   , false .
      state.error = action.payload; // catch  error  state.error .
    },

    [getDetails.pending]: (state) => {
      state.isLoading = true; //     true .
    },
    [getDetails.fulfilled]: (state, action) => {
      // console.log(action); //     
      state.isLoading = false; //   , false .
      state.detail = action.payload; // Store  movies   movies .
      // console.log(state.detail);
    },
    [getDetails.rejected]: (state, action) => {
      state.isLoading = false; //  ,   , false .
      state.error = action.payload; // catch  error  state.error .
    },

    [getMypage.pending]: (state) => {
      state.isLoading = true; //     true .
    },
    [getMypage.fulfilled]: (state, action) => {
      // console.log(action);        //     
      state.isLoading = false; //   , false .
      state.mylist = action.payload; // Store  movies   movies .
      // console.log(state.movies);
    },
    [getMypage.rejected]: (state, action) => {
      state.isLoading = false; //  ,   , false .
      state.error = action.payload; // catch  error  state.error .
    },

    [onLikePost.pending]: (state) => {
      state.isLoading = true; //     true .
    },
    [onLikePost.fulfilled]: (state, action) => {
      // console.log(action); //     
      state.isLoading = false; //   , false .
      state.like = action.payload; // Store  movies   movies .
      // console.log(state.like);
    },
    [onLikePost.rejected]: (state, action) => {
      state.isLoading = false; //  ,   , false .
      state.error = action.payload; // catch  error  state.error .
    },
  },
});
export default movies.reducer;
